"use client";

import dynamic from "next/dynamic";
import 'react-quill/dist/quill.snow.css'; // 에디터 스타일을 불러옵니다.

// ssr: false 옵션으로 서버 사이드 렌더링을 비활성화합니다.
const ReactQuill = dynamic(() => import('react-quill'), { ssr: false });

interface QuillEditorProps {
  value: string;
  onChange: (value: string) => void;
}

export default function QuillEditor({ value, onChange }: QuillEditorProps) {
  // 에디터 툴바 옵션 설정
  const modules = {
    toolbar: [
      [{ 'header': [1, 2, 3, false] }],
      ['bold', 'italic', 'underline', 'strike'],
      [{ 'list': 'ordered'}, { 'list': 'bullet' }],
      [{ 'color': [] }, { 'background': [] }],
      ['link', 'image'],
      ['clean']
    ],
  };

  return (
    <ReactQuill
      theme="snow"
      modules={modules}
      value={value}
      onChange={onChange}
      className="bg-white"
    />
  );
} 